/*
 * Reset all button
 */
.filter-reset-all {
	appearance: none;
	padding: 5px 10px;
	color: $uicolor;
	background: transparent;
	border: 1px solid $uicolor;
	border-radius: 3px;
	opacity: 0.25;

	.filter-selected &,
	.filter-unselected &,
	body[class*="stars-filter-"] & {
		opacity: 1;

		&:hover {
			color: $text;
			border-color: $text;
		}
	}
}