/* Light Color Scheme */

/* Colors */
$site-background: #fcfcfc;
$imgbox-background: #ffffff;
$imgbox-background-interpolation: ffffff;
$imgbox-background-variation: darken( $imgbox-background, 2% );
$flash-color: darken($imgbox-background, 35%);

$alert-color: #e03;
$success-color: #7ad03a;
$warning-color: #f08a24;
$info-color: #0074a2;
$draft-color: purple;
$delivery-draft-color: $info-color;
$delivered-color: $info-color;

$primary: #2f92a7;
$primary-variation: lighten( $primary, 30% );
$primary-variation-2: darken( $primary, 35% );

$border-line: #ddd;
$text: #575757;
$text-interpolation: 575757;
$selection-count: $text;
$gradient: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
$fade-out-gradient: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
$active-text-area: $imgbox-background;

$uicolor: lighten( $text, 30% );
$uicolor-interpolation: a4a4a4;

$box-shadow: rgba(0,0,0,0.15);
$star-shadow-color: rgb( 0 0 0 / 0.15 );

/* Button Color */
$button-txt: #999;
$button-bg: $primary;

$primary-button-txt: white;
$primary-button-bg: $primary;

/* Images */
$loading-gif: url(../images/ripple-light.gif) no-repeat center center;
$loading-gif-alt: url(../images/ripple-light.gif);
$loading-gif-alt-inverted: url(../images/ripple-dark.gif);

/* Set CSS custom properties */
:root {
	--picu-primary: #2f92a7;
	--picu-star-color: var(--picu-primary);

	--picu-text-color: #575757;
	--picu-site-background: #fcfcfc;
	--picu-imgbox-background: #ffffff;
	--picu-border-line: #ddd;
	--picu-border-line-strong: #ccc;

	--picu-alert-color: #e03;
	--picu-success-color: #7ad03a;
	--picu-warning-color: #f08a24;
	--picu-info-color: #0074a2;
	--picu-draft-color: purple;
}

/* Includes */
@import "inc/variables";
@import "inc/picu";