		width: 11.1111111111%;
	}

	@media only screen and (min-width: 2900px) {
		width: 10%;
	}
} // .thumbsize-small .picu-gallery-item


.thumbsize-large .picu-gallery-item {
	@media only screen and (min-width: 500px) {
		width: 100%;
	}

	@media only screen and (min-width: 740px) {
		width: 50%;
	}

	@media only screen and (min-width: 1000px) {
		width: 33.33333%;
	}

	@media only screen and (min-width: 1250px) {
		width: 25%;
	}

	@media only screen and (min-width: 1500px) {
		width: 20%;
	}

	@media only screen and (min-width: 2000px) {
		width: 16.66666%;
	}

	@media only screen and (min-width: 2500px) {
		width: 14.2857143%;
	}

	@media only screen and (min-width: 2900px) {
		width: 12.5%;
	}
} // .thumbsize-big .picu-gallery-item


/*
 * picu Lightbox
 */

/* lightbox-view.js */
/* picu-lightbox.html */

.picu-lightbox {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: $site-background;
	z-index: 15;

	&.open {
		display: block;
	}

	.has-picu-admin-bar & {
		top: $picu-admin-bar-height;
		height: calc( 100% - #{$picu-admin-bar-height} );
	}

}

.picu-lightbox-inner {
	position: relative;
	padding: 20px;
	padding-bottom: $statusbarheight + 20;
	width: 100%;
	height: 100%;

	.picu-lightbox-image-container {
		position: relative;
		width: 100%;
		height: 100%;
		background-color: $site-background;

		&:before {
			content: '';
			display: block;
			padding-top: 100%;
		}
	}

	img {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		margin: auto;
		max-width: 100%;
		max-height: 100%;
		width: auto;
		height: auto;
		border: 5px solid transparent;
		background: $loading-gif;
		background-size: 20px 20px;

		&.selected {
			border: 5px solid $primary;
		}
	}

	&.mark-comment {
		display: block;
		padding: 0;

		&.comment-box-is-collapsed .picu-lightbox-image-container {
			height: 100%;
		}

		.picu-lightbox-image-container-wrap {
			height: calc(100% - 140px);
			overflow: auto;
			background-color: $imgbox-background;

			@media only screen and (min-width: $break-small) {
				height: calc(100% - 70px);
			}
		}

		.picu-lightbox-image-container {
			position: relative;
			flex: 1 1 auto;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: nowrap;
			padding: 20px;
			width: 100%;
			height: auto;
		}

		img {
			max-width: calc(100% - 40px);
			max-height: calc(100% - 40px);
		}

		@media only screen and (min-width: $smartphone) {
			.picu-lightbox-image-container-wrap {
				display: flex;
				flex-wrap: nowrap;
				justify-content: center;
				align-items: stretch;
				padding-bottom: $statusbarheight;
				width: 100%;
				height: 100%;
				background: none;
			}

			.picu-lightbox-image-container {
				position: relative;
				flex: 1 1 auto;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-wrap: nowrap;
				padding: 20px;
				width: 100%;
				height: 100%;

				&:before {
					display: none;
				}

				img {
					max-width: calc(100% - 40px);
					max-height: calc(100% - 40px);
				}
			}
		}
	}
}

.uses-mark-comment .picu-lightbox-inner img {
	cursor: cell;
}

.status-approved.uses-mark-comment .picu-lightbox-inner img,
.status-failed.uses-mark-comment .picu-lightbox-inner img,
.uses-mark-comment .picu-lightbox-inner.comment-box-is-collapsed img {
	cursor: default;
}

.picu-comment-box-toggle {
	top: 21px;
	right: 24px;
	position: absolute;
	cursor: pointer;

	span {
		@include visually-hidden;
	}

	svg {
		fill: $text;
		width: 25px;
		height: 22.5px;
	}
}

.picu-comment-box {
	flex: 0 0 auto;
	width: 100%;
	background-color: $imgbox-background;
	border-top: 1px solid $border-line;

	@media only screen and (min-width: $smartphone) {
		width: 240px;
		border-left: 1px solid $border-line;
		overflow-x: hidden;
		overflow-y: auto;
	}

	@media only screen and (min-width: 800px) {
		width: 360px;
	}

	@media only screen and (min-width: 1024px) {
		width: 400px;
	}
}

.comment-box-is-collapsed .picu-comment-box {
	flex: 0 0 74px;
	width: 74px;
	border-color: transparent;
	background-color: transparent;
}

.picu-comment-box-inner {
	padding: 20px;
	overflow-x: hidden;
	overflow-y: auto;
}

.comment-box-is-collapsed .picu-comment-box-inner {
	display: none;
}

.picu-comment-box-header {
	display: block;
	margin-bottom: 20px;
	font-weight: 700;
}

.picu-comment-box-intro {
	margin-bottom: 30px;
	padding-top: 10px;
	color: $uicolor;
	text-align: center;
}

.picu-lightbox-inner.mark-comment.has-markers .picu-comment-box-intro {
	display: none;
}

.picu-comment {
	position: relative;
	margin: 0 0 20px 0;

	&.editable {
		.picu-comment-textarea {
			max-height: none;
			background-color: $active-text-area;
			border: 1px solid transparent;
			box-shadow: 0 0.4rem 1.4rem -0.4rem rgba(0,0,0,0.4);

			&:after {
				width: 0;
				height: 0;
			}
		}
	}

	&.editable.is-focused {
		animation: none;
		&:hover {
			&:after {
				transform: scale3d( 1, 1, 1);
			}
		}
	}

	.status-failed &,
	.status-approved & {
		.picu-comment-textarea {
			max-height: none;

			&:after {
				width: 0;
				height: 0;
			}
		}
	}
}

.picu-comment-textarea {
	position: relative;
	margin: 0 0 10px 0;
	padding: 15px 25px 15px 15px;
	width: 100%;
	min-height: 24px;
	max-height: 180px;
	overflow: hidden;
	color: $text;
	background-color: $imgbox-background;
	border: 1px solid $border-line;
	outline: none;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 20px;
		background: $fade-out-gradient;
	}
}

.picu-comment-meta {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin: -10px 0 10px;
}

.picu-comment.editable .picu-comment-meta {
	display: none;
}

.picu-comment-meta__time {
	padding: 2px 15px;
	font-size: 10px;
}


.picu-comment-marker {
	display: block;
	position: absolute;
	top: 20px;
	right: 15px;
	width: 13px;
	height: 13px;
	border: 2px solid $primary;
	border-radius: 50%;

	.is-focused &,
	.picu-comment:hover &,
	.editable & {
		transform: scale3d( 1.5, 1.5, 1.5 );
		background-color: rgba( $primary-variation, 0.8 );

		&:before {
			content: '';
			position: absolute;
			left: 20%;
			top: 20%;
			width: 60%;
			height: 60%;
			background-color: $primary;
			border-radius: 50%;
		}
	}
}

.picu-comment-controls {
	display: none;
}

.editable .picu-comment-controls {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.picu-comment-control {
	display: flex;
	flex-wrap: nowrap;
	font-size: 14px;
	color: darken( $button-txt, 50% );
	justify-content: center;
	align-items: center;
	cursor: pointer;

	svg {
		margin-right: 5px;
		width: 10px;
		height: 10px;
		fill: darken( $button-txt, 50% );
	}

	&:hover {
		color: $button-txt;

		svg {
			fill: $button-txt;
		}
	}

	&.picu-delete-comment {
		padding-left: 1em;
	}

	&.picu-save-comment {
		padding-right: 1em;
	}
}

.picu-add-comment {
	display: flex;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
	cursor: pointer;

	svg {
		margin-right: 5px;
		width: 16px;
		height: 16px;
		fill: $text;
	}
}

.is-editing .picu-add-comment {
	display: none;
}

.marker {
	position: absolute;
	display: block;
	width: 15px;
	height: 15px;
	border: 2px solid $primary;
	border-radius: 50%;
	outline: none;
	box-shadow:
		0 0 6px rgba( 0, 0, 0, 0.5 ),
		0 0 0 0 rgba( $primary, 0.8 ),
		0 0 0 0 rgba( $primary-variation, 1 );
	cursor: pointer;
	opacity: 1;
	transition: opacity 0.25s ease-in, transform 0.05s ease-in;

	&:hover,
	&.is-focused {
		transform: scale3d( 1.5, 1.5, 1.5 );
		background-color: rgba( $primary-variation, 0.6 );
		animation: pulsate 0.8s infinite;

		&:before {
			content: '';
			position: absolute;
			left: 20%;
			top: 20%;
			width: 60%;
			height: 60%;
			background-color: $primary;
			border-radius: 50%;
		}
	}

	&.is-active {
		background-color: $primary-variation;
		transform: scale3d(1.5, 1.5, 1.5);

		&:before {
			content: '';
			position: absolute;
			left: 20%;
			top: 20%;
			width: 60%;
			height: 60%;
			background-color: $primary;
			border-radius: 50%;
		}

		&.is-focused {
			animation: none;
		}

	}

	&.is-waiting {
		opacity: 0;
		transition: opacity 0.25s ease-in;
	}
}

@keyframes pulsate {
	70% {
		box-shadow:
			0 0 6px rgba( 0, 0, 0, 0.5 ),
			0 0 2px 15px rgba( 0, 0, 0, 0 ),
			0 0 2px 7px rgba( 0, 0, 0, 0 );
	}

	100% {
		box-shadow:
			0 0 6px rgba( 0, 0, 0, 0.5 ),
			0 0 0 0 rgba( 0, 0, 0, 0 ),
			0 0 0 0 rgba( 0, 0, 0, 0 );
	}
}

.comment-box-is-collapsed .marker {
	display: none;
}


.picu-lightbox-navigation {
	position: absolute;
	left: 0;
	bottom: 0;
	display: grid;
	grid-template-columns: 74px auto auto repeat(3, 74px);
	width: 100%;
	height: $statusbarheight;
	text-align: right;
	background-color: $imgbox-background;
	border-top: 1px solid $border-line;
	transition: all 0.5s;
	z-index: 1;

	.picu-pro & {
		grid-template-rows: 70px 70px;
		height: $statusbarheight * 2;

		@media only screen and (min-width: $lightbox-switch) {
			grid-template-rows: 70px;
			height: $statusbarheight;
		}
	}

	@media only screen and (min-width: 400px) {
		grid-template-columns: 74px calc( 50% - 74px ) calc( 50% - 222px ) repeat(3, 74px);
	}
	
	@media only screen and (min-width: $lightbox-switch) {
		height: $statusbarheight;
		grid-template-columns: 74px auto auto 178px 74px 74px 74px;
		grid-template-rows: 70px;
	}

    a {
        display: inline-block;
        margin: 0;
        padding: 2.6em 2.5em;
        height: $statusbarheight;
        font-size: 0.7em;
        line-height: 1;
        text-transform: uppercase;
        border-left: 1px solid $border-line;
        white-space: nowrap;

		@media (hover) {
			&:hover {
				color: $button-txt;
				background-color: $button-bg;

				svg {
					fill: white;
				}
			}
		}

        span {
            @include visually-hidden;
        }
        svg {
            width: 1.5em;
            height: auto;
            fill: $text;
        }
	}
}

.picu-lightbox-close {
	grid-row: 2/3;
	grid-column: 1/2;
	border-left: none;
	border-right: 1px solid $border-line;
	z-index: 1;

	@media only screen and (min-width: $lightbox-switch) {
		grid-row: 1/2;
	}
}

.picu-img-name {
	position: relative;
	grid-column: 2/4;
	grid-row: 2/3;
	display: block;
	padding: 1.5em 2em 1.5em 1em;
	height: $statusbarheight;
	font-weight: 100;
	color: transparentize( $text, 0.25 );
	text-align: left;
	white-space: nowrap;
	overflow: hidden;

	@media only screen and (min-width: $lightbox-switch) {
		grid-row: 1/2;
		padding-left: 2em;
	}

	.picu-download & {
		@media only screen and (min-width: $lightbox-switch) {
			grid-column: 2/3;
		}
	}

	&:after {
		content: '';
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: 30px;
		height: 100%;
		background: linear-gradient( -90deg, $imgbox-background 0%, transparent 100% );
	}
}

.picu-lightbox-select {
	grid-column: 4/5;
	grid-row: 2/3;

	@media only screen and (min-width: $lightbox-switch) {
		grid-column: 5/6;
		grid-row: 1/2;
	}

	&.selected {
		svg {
			fill: $primary;
		}

		@media (hover) {
			&:hover {
				svg {
					fill: white;
				}
			}
		}
	}
}

.picu-lightbox-close,
.picu-lightbox-prev,
.picu-lightbox-next {
	width: 74px;
	height: 70px;
}

.picu-lightbox-prev {
	grid-column: 5/6;
	grid-row: 2/2;

	@media only screen and (min-width: $lightbox-switch) {
		grid-row: 1/2;
		grid-column: 6/7;
	}
}

.picu-lightbox-next {
	grid-column: 6/7;
	grid-row: 2/2;

	@media only screen and (min-width: $lightbox-switch) {
		grid-row: 1/2;
		grid-column: 7/8;
	}
}


.picu-lightbox__actions {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 20px;
	width: 100%;
	grid-column: 1/6;
	grid-row: 1/2;
	display: none;
	border-bottom: 1px solid $border-line;

	@media only screen and (min-width: $lightbox-switch) {
		grid-row: 1/2;
		grid-column: 3/4;
	}

	.picu-download & {
		display: flex;
		grid-column: 4/7;

		@media only screen and (min-width: 400px) {
			grid-column: 3/7;
		}

		@media only screen and (min-width: $lightbox-switch) {
			justify-content: flex-end;
			grid-column: 3/4;
		}
	}
}

.picu-lightbox__star-rating {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	border-left: 1px solid $border-line;
	grid-column: 1/7;
	grid-row: 1/2;
	border-bottom: 1px solid $border-line;

	@media only screen and (min-width: $lightbox-switch) {
		grid-column: 2/3;
		grid-column: 4/5;
	}

	.picu-download & {
		grid-column: 1/5;

		@media only screen and (min-width: 400px) {
			grid-column: 1/3;
		}

		@media only screen and (min-width: $lightbox-switch) {
			grid-column: 4/5;
		}
	}
}

.picu-lightbox__star-rating__button {
	appearance: none;
	position: relative;
	margin: 0;
	padding: 4px;
	width: 28px;
	height: 28px;
	background: transparent;
	border: none;
	cursor: pointer;

	&:before {
		content: '';
		position: absolute;
		z-index: -1;
		top: calc( 50% - 2px );
		left: calc( 50% - 2px );
		display: block;
		width: 4px;
		height: 4px;
		background-color: $text;
		border-radius: 50%;
		opacity: 1;
		transition: opacity 0.35s;
	}

	svg {
		display: none;
		width: 20px;
		height: 20px;
		fill: $imgbox-background;
		stroke: $uicolor;
	}
}

.picu-lightbox__star-rating__button:hover svg,
.picu-lightbox__star-rating__button:has(~ .picu-lightbox__star-rating__button:hover) svg {
	display: block;
	fill: $imgbox-background;
	stroke: $uicolor;
}

// Set active colors
.picu-lightbox[class*='star-rating-1'] .picu-lightbox__star-rating__button--1 svg,
.picu-lightbox[class*='star-rating-2'] .picu-lightbox__star-rating__button--1 svg,
.picu-lightbox[class*='star-rating-2'] .picu-lightbox__star-rating__button--2 svg,
.picu-lightbox[class*='star-rating-3'] .picu-lightbox__star-rating__button--1 svg,
.picu-lightbox[class*='star-rating-3'] .picu-lightbox__star-rating__button--2 svg,
.picu-lightbox[class*='star-rating-3'] .picu-lightbox__star-rating__button--3 svg,
.picu-lightbox[class*='star-rating-4'] .picu-lightbox__star-rating__button--1 svg,
.picu-lightbox[class*='star-rating-4'] .picu-lightbox__star-rating__button--2 svg,
.picu-lightbox[class*='star-rating-4'] .picu-lightbox__star-rating__button--3 svg,
.picu-lightbox[class*='star-rating-4'] .picu-lightbox__star-rating__button--4 svg,
.picu-lightbox[class*='star-rating-5'] .picu-lightbox__star-rating__button--1 svg,
.picu-lightbox[class*='star-rating-5'] .picu-lightbox__star-rating__button--2 svg,
.picu-lightbox[class*='star-rating-5'] .picu-lightbox__star-rating__button--3 svg,
.picu-lightbox[class*='star-rating-5'] .picu-lightbox__star-rating__button--4 svg,
.picu-lightbox[class*='star-rating-5'] .picu-lightbox__star-rating__button--5 svg {
	display: block;
	fill: var( --picu-star-color );
	stroke: var( --picu-star-color );
}

body.status-approved .picu-lightbox__star-rating__button {
	pointer-events: none;
	cursor: default;
}

body.status-approved .picu-lightbox__star-rating__button:before {
	display: none;
}


/*
 * Status Bar
 */
.picu-status-bar {
	position: fixed;
	left: 0;
	bottom: 0;
	display: grid;
	grid-template-columns: 3fr 0 3fr 3fr;
	margin-bottom: 0;
	padding: 0 1rem;
	width: 100%;
	height: $statusbarheight;
	background-color: $imgbox-background;
	border-top: 1px solid $border-line;
	transition: padding 0.25s ease;
	z-index: 11;

	@media only screen and (min-width: 320px) {
		grid-template-columns: 1fr 1fr 2fr 4fr 4fr;
	}

	@media only screen and (min-width: 450px) {
		grid-template-columns: 2fr 1fr 1fr 4fr 4fr;
	}

	@media only screen and (min-width: 500px) {
		grid-template-columns: 1fr 1fr 2fr 4fr 4fr;
	}

	@media only screen and (min-width: 450px) {
		.filter-selected &, .filter-unselected & {
			padding: 0 1rem;
		}
	}
}



/*
 * Filter UI
 */
.picu-display-filter {
	position: relative;
	grid-column: 1 / 2;
	align-self: center;
	padding: 0 0 0 20px;
	padding: 0;
	transition: padding 0.25s ease;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 22px;
		left: 2px;
		width: 17px;
		height: 39px;
		border-radius: 17px;
		border: 1px solid $uicolor;
		z-index: -1;
		transition: left 0.25s ease;
	}
}

.filter-selected .picu-display-filter, .filter-unselected .picu-display-filter {
	padding-left: 20px;

	&:before {
		left: 22px;
	}

}

.picu-filter-selected, .picu-filter-unselected {
	display: block;
	margin: 0 0 3px;
	padding: 2px 4px;
	line-height: 0;
	white-space: nowrap;
	border-radius: 1px 0 0 1px;

	.picu-filter-label {
		display: none;
		font-size: 9px;
		letter-spacing: 0.5px;
		font-weight: 700;
		color: $uicolor;
		text-transform: uppercase;

		@media only screen and (min-width: 450px) {
			display: inline;
		}
	}

	&:hover {
		span {
			color: $text;
		}
		svg {
			fill: $text;
		}
	}
}

.filter-selected .picu-filter-selected span, .filter-unselected .picu-filter-unselected span {
	color: $text;
}

.picu-filter-icon {
	display: inline-block;
	margin: 0 5px 0 0;
	padding-top: 3px;
	width: 15px;
	height: 15px;
	border: 1px solid transparent;
	text-align: center;
	border-radius: 15px;

	svg {
		width: 7px;
		height: 7px;
		fill: $uicolor;
	}

	.filter-selected .picu-filter-selected &, .filter-unselected .picu-filter-unselected & {
		background-color: $text;

		svg {
			fill: $imgbox-background;
		}
	}
}

.picu-filter-reset {
	position: absolute;
	left: 0;
	top: 9px;
	visibility: hidden;
	opacity: 0;
	transition: visibility 0s, opacity 0.25s linear;

	.filter-selected &, .filter-unselected & {
		visibility: visible;
		opacity: 1;
	}

	svg {
		width: 12px;
		height: 12px;
		fill: $uicolor;
	}

	&:hover svg {
		fill: $text;
	}

	span {
		display: none;
	}
}

.picu-stars-filter {
	grid-column: 2 / 3;
	align-self: center;
	position: relative;
	display: flex;
	padding-left: 20px;
}

.picu-stars-filter__filter {
	display: block;
	margin: 0;
	padding: 2px;
	width: 20px;
	height: 20px;
	background: transparent;
	border: none;

	svg {
		max-width: 100%;
		height: auto;
		stroke: $uicolor;
		fill: none;
	}
}

// Set fixed stars filter
.stars-filter-1 .picu-stars-filter__filter:has(~ .picu-stars-filter__filter[data-stars='2']) svg,
.stars-filter-2 .picu-stars-filter__filter:has(~ .picu-stars-filter__filter[data-stars='3']) svg,
.stars-filter-3 .picu-stars-filter__filter:has(~ .picu-stars-filter__filter[data-stars='4']) svg,
.stars-filter-4 .picu-stars-filter__filter:has(~ .picu-stars-filter__filter[data-stars='5']) svg,
.stars-filter-5 .picu-stars-filter__filter svg {
	fill: $text;
	stroke: $text;
}

// Reset star filter styling on hover
.picu-stars-filter .picu-stars-filter__filter:hover svg {
	stroke: $uicolor;
	fill: none;
}

// Set star filter when hovering over stars
.picu-stars-filter:hover .picu-stars-filter__filter:hover svg,
.picu-stars-filter .picu-stars-filter__filter:has(~ .picu-stars-filter__filter:hover) svg {
	fill: $text;
	stroke: $text;
}


